/* >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
body {
  background-color: var(--bone);
  color: var(--dark-green);
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
}

main {
  flex: 1;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.mountain-bg {
  background-image: url("./assets/images/tent-door.jpg");
  background-size: cover;
  background-position: center 30%;
  color: var(--bone);
}

input {
  border: 1px solid var(--dark-green);
  border-radius: 10px;
  padding: 10px;
  margin: 5px;
}

.btn-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn {
  border: none;
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
  width: 70px;
  background-color: var(--dark-green);
  color: var(--bone);
  transition: background-color 0.15s, color 0.15s;
}

.btn:hover {
  background-color: var(--light-green);
  color: var(--bone);
}

.btn-faded {
  background-color: var(--bone);
  border: none;
  color: var(--dark-green);
}

.page-cover {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.6);
}

.padding {
  padding: 30px;
}

.margin-top {
  margin-top: 20px;
}

.nothing-here {
  margin-left: 5%;
  color: var(--light-green);
}